cc.Class({
    extends: cc.Component,

    properties: {
        reelNum: 5,
        reelPrefab: cc.Prefab,
        reelWidth: 100,
        reelHolder: cc.Node,
        cameraZ: 100,
    },

    onLoad() {
        for (let i=0; i < this.reelNum; i++) {
            const reel = cc.instantiate(this.reelPrefab);
            reel.setParent(this.reelHolder);
            reel.setPosition((i - Math.floor(this.reelNum/2)) * this.reelWidth, 0, 0);
        }
    }
});
